<template>
  <div class="retrieve">
    <van-nav-bar
      title="设置密码"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <van-form @submit="commit">
      <van-field
        v-model="userInfo.phone"
        name="手机号"
        label="手机号"
        placeholder="11位手机号"
      />
      <van-field
        v-model="userInfo.password"
        type="password"
        name="新密码"
        label="新密码"
        placeholder="6-16个字符"
      />

      <div class="btn-box">
        <van-button round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
import { valid } from "../utils/valid";
import { utils } from "../utils/utils";
export default {
  name: "Retrieve",
  data() {
    return {
      userInfo: {
        phone: "",
        password: "",
      },
    };
  },

  methods: {
    //返回上一级
    back() {
      this.$router.go(-1);
    },
    commit() {
      if (valid.validForm(this.userInfo, this)) {
        //验证通过
        //发起请求
        //加载提示
        this.$toast.loading({
          forbidClick: true,
          duration: 0,
          message: "加载中...",
        });
        this.axios({
          method: "POST",
          url: "/retrievePassword",
          data: {
            appkey: this.appkey,
            phone: this.userInfo.phone,
            password: this.userInfo.password,
          },
          transformRequest: utils.transformRequest,
        })
          .then((d) => {
            this.$toast.clear();
            if (d.data.code == "L001") {
              this.$router.push({ name: "Login" });
            } else {
              this.$toast(d.data.msg);
            }
          })
          .catch((err) => {});
      }
    },
  },
};
</script>

<style lang="less" scoped>
.retrieve {
  padding-top: 96px;
  .btn-box {
    margin-top: 16px;
    padding: 0 10px;
  }
}
</style>